.container-roadmap {
  height: 100vh;
  background-color: darkcyan;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 100vw;
}

.container-roadmap > .main-mobile {
  display: none;
}

.container-roadmap > .main-desktop {
  height: 100%;
  width: 100%;
  background-color: aquamarine;
  margin: 1rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.container-roadmap > .main-desktop > .row-2 {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  background-color: blueviolet;
}

.container-roadmap > .main-desktop > .row-2 > img {
  width: 100%;
  height: auto;
}

.container-roadmap > .main-desktop > .row-2 > .roadmap-mobile {
  display: none;
}

.container-roadmap .card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #0a74cb7e;
  width: 40rem;
  height: 16rem;
  padding: 2rem;
  border-radius: 40px 10px 40px 10px;
  font-size: 1.4rem;
  text-align: center;
}

.container-roadmap > .main-desktop > .row-1 {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 4rem 20rem 4rem 20rem;
  margin: 0 20rem 0 20rem;
}

.container-roadmap > .main-desktop > .row-3 {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4rem 2rem 4rem 2rem;
}

@media (max-width: 1440px) {
  
}

@media (max-width: 1024px) {
  .container-roadmap {
    height: 80vh;
  }
}

@media (max-width: 768px) {
  .container-roadmap {
    height: 100vh;
  }

  .container-roadmap > .main-desktop {
    display: none;
  }

  .container-roadmap > .main-mobile > .roadmap-mobile {
    height: 100%;
    margin-right: 2rem;
  }

  .container-roadmap > .main-mobile {
    display: grid;
    grid-template-columns: 1fr, 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 
    "row-2 c1";
    height: 80vh;
    width: 50%;
    padding: 6rem 2rem 2rem 2rem;
  }
    
  .container-roadmap > .main-mobile > .col-right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }

  .container-roadmap > .main-mobile > .col-right > .card {
    padding: 2rem;
    font-size: 1.6rem;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 42rem;
  height: 12rem;
  }

  .container-roadmap > .main-mobile > .col-right > .card-3 {
    border-radius: 10px 40px 10px 40px;
  }
  .container-roadmap > .main-mobile > .col-right > .card-4 {
    border-radius: 40px 40px 40px 40px;
  }
  .container-roadmap > .main-mobile > .col-right > .card-2 {
    border-radius: 10px 40px 10px 40px;
  }
  .container-roadmap > .main-mobile > .col-right > .card-5 {
    border-radius: 40px 10px 40px 10px;
  }
    
}

@media (max-width: 375px) {

  .container-roadmap > .main-mobile > .col-right > .card {
    padding: 1.2rem;
    font-size: 1.4rem;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 38rem;
    height: 10rem;
  }
}



