    height: 38rem;

  }
}

@media (max-width: 650px) {
  .container-team > .grid-team {
    grid-template-columns: auto;
    grid-template-rows: 1fr, 1fr, 1fr, 1fr, 1fr, 1fr;
    grid-template-areas: 
    "card-1"
    "card-1"
    "card-1"
    "card-1"
    "card-1"
    "card-1";
    grid-gap: 2rem;
    gap: 2rem;
  }

  .container-team > .grid-team > .card-lead {
    width: 52rem;
    height: 40rem;

  }

  .container-team > .grid-team > .card-lead > img {
    width: 16rem;
  height: 16rem;
  }
}
