.fixed-navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 10rem;
  background: linear-gradient(180deg, rgba(8, 135, 212, 1) 0%, rgba(8, 135, 212, .5) 100%);
  z-index: 50;
  font-size: 2rem;
}

.nav-bar {
  position: relative;
  z-index : 20;
  display : flex;
}

.nav-bar > .page-selected {
  position: absolute;
  right: 43rem;
  top: 1rem;
  width: 8rem;
  height: 8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 50%;
}

.nav-bar > .page-selected > img {
  color: #fff;
}

.nav-bar>a>img {
  position: relative;
  top     : 16%;
  left    : 6%;
  width   : 6rem;
  height  : 6rem;
}

.nav-bar>.nav-items>ul {
  display: flex;
}

.nav-bar>.nav-items>ul>li {
  display        : flex;
  justify-content: space-between;
  padding        : 8px;
  margin-right: 20px;
}

.nav-bar>.nav-items>ul>li>a {
  text-decoration: none;
  color          : var(--font-primary);
  text-align     : center;
  font-weight    : bold;
  padding-top    : 8px;
  padding-bottom : 8px;
  margin-top     : 8px;
  margin-bottom  : 8px;
  min-width      : 100px;
  min-height     : 42px;
  height         : 60px;
}

.nav-bar>.nav-items>ul>li>a:hover {
  border-bottom: 2px solid #fff;
}

/* Style The Dropdown Button */
.dropbtn {
  color           : #fff;
  padding-top     : 8px;
  padding-bottom  : 8px;
  margin-top      : 18px;
  margin-bottom   : 8px;
  min-width      : 100px;
  min-height     : 42px;
  text-align      : center;
  font-size       : 2rem;
  font-weight     : bold;
  border          : none;
  cursor          : pointer;
  background-color: transparent;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display : inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display         : none;
  position        : absolute;
  background-color: #f9f9f9;
  min-width       : 158px;
  border-radius   : 4px;
  box-shadow      : 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index         : 1;
  transition      : all .3s;
}

/* Links inside the dropdown */
.dropdown-content a {
  color          : black;
  font-size      : 1.8rem;
  padding        : 12px 16px;
  text-decoration: none;
  display        : block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  background-color: #f1f1f1
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover .dropbtn {
  background-color: transparent;
}
